if(typeof(i18n) == 'undefined') {
    console.error('Class i18n not defined');
} else {
    i18n.addDictionary('fr', {
        "Admin.LINKLABEL_PHONE": "Lien vers numéro de téléphone",
        "Admin.LINK_PHONE": "Inserer un lien vers un numéro de téléphone",
    });
}


